import React, { Component } from 'react';
import Aux from '../../../hoc/Auxillary/Auxillary';
import Button from '../../UI/Button/Button';

class OrderSummary extends Component {
  //This could have been a fuctional component. Just wanted to check with component
  componentDidUpdate() {
    console.log('[OrderSummary] will update');
  }

  render(){
  const ingredientSummary = Object.keys(this.props.ingredients).map(igKey=>{
    return <li key={igKey}>
              <span style={{TextTransform:'capitalize'}}> {igKey} </span> : {this.props.ingredients[igKey]}
          </li>
  });
  return(
    <Aux>
      <h3>Your Order</h3>
      <p>A delicious Burger with the following igredients :</p>
      <ul>
        {ingredientSummary}
      </ul>
      <p><strong>Total Amount : ${this.props.price.toFixed(2)}</strong></p>
      <p>Continue to Check-Out ?</p>
      <Button btnType="Danger" clicked={this.props.purchaseCancelled}>CANCEL</Button>
      <Button btnType="Success" clicked={this.props.puchaseContinued}>CONTINUE</Button>
    </Aux>
  );
 }
}

export default OrderSummary;



